<template>
  <div class="">
    <h1>Lista di conservazioni</h1>
    <hr />
    <div class="row">
      <div class="col-lg-4 form-group">
        <label for="">IDCO</label>
        <input
          type="text"
          class="form-control"
          v-model="idco"
          placeholder="inserisci IDCO"
          v-on:keyup.enter="submit"
        />
      </div>
      <div class="col-lg-4 form-group">
        <label for="">ID Origine</label>
        <input
          type="text"
          class="form-control"
          v-model="origin_id"
          placeholder="inserisci ID Origine"
          v-on:keyup.enter="submit"
        />
      </div>
      <div class="col-lg-4 form-group">
        <label for="">Stato</label>
        <select v-model="status" class="form-control">
          <option value="null" selected>Seleziona</option>
          <option
            v-for="(item, index) in statusList"
            :key="index"
            :value="item.id"
          >
            {{ item.message }}
          </option>
        </select>
      </div>
      <div class="col-lg-4 form-group">
        <label for="">Ordina per campo</label>
        <select v-model="sort" class="form-control">
          <option value="null" disabled selected>Seleziona</option>
          <option
            v-for="(item, index) in orderList"
            :key="index"
            :value="item.key"
          >
            {{ item.message }}
          </option>
        </select>
      </div>
      <div class="col-lg-4 form-group">
        <label for="">Ordina asc | desc</label>
        <select v-model="order" class="form-control">
          <option value="desc" selected>desc</option>
          <option value="asc">asc</option>
        </select>
      </div>
      <div class="col-lg-4">
        <label for="">Quantita'</label>
        <select v-model="limit" class="form-control">
          <option value="10">10</option>
          <option value="30">30</option>
          <option value="50">50</option>
          <option value="100" selected>100</option>
          <option value="200">200</option>
          <option value="500">500</option>
          <option value="1000">1000</option>
        </select>
      </div>
      <div class="col-lg-4">
        <button
          class="btn btn-success"
          @click="submit"
          v-on:keyup.enter="submit"
        >
          Aggiorna
        </button>
      </div>
    </div>
    <br />
    <hr />
    <h5>totali: {{ tot }}</h5>
    <br />
    <div class="row" v-if="hasData">
      <table class="table table-striped table-lg" style="width: 100%">
        <thead>
          <th>ID Azienda</th>
          <th>IDCO</th>
          <th>Ras. Soc</th>
          <th>ID Origine</th>
          <th>Origine</th>
          <th>Modello</th>
          <th>ge</th>
          <th>ve</th>
          <th>co</th>
          <th>quantita da conservare</th>
          <th>Stato</th>
          <th>error</th>
          <th>Data Creata</th>
          <th>Data Modifica</th>
          <th>Operazioni</th>
        </thead>
        <tbody>
          <tr v-for="(item, index) in data.rows" :key="index">
            <td>{{ item.company_id }}</td>
            <td>{{ item.idco }}</td>
            <td>{{ item.company_name }}</td>
            <td>{{ item.origin_id }}</td>
            <td>{{ item.origin_name }}</td>
            <td>{{ item.model }}</td>
            <td>{{ item.ge }}</td>
            <td>{{ item.ve }}</td>
            <td>{{ item.co }}</td>
            <td>{{ item.retention_quantity }}</td>
            <td>{{ getStatusText(item.status) }}</td>
            <td>{{ item.error | liveSubstr }}</td>
            <td>{{ item.created_at }}</td>
            <td>{{ item.updated_at }}</td>
            <td>
              <a
                title="Dettagli PDV in tabella CodeConservazioni.Coda"
                class="btn btn-link"
                data-toggle="modal"
                data-target="#pdv_detail_modal_btn"
                @click="detailRetention(item.retention_id)"
              >
                <i class="fa fa-list" aria-hidden="true"></i>
              </a>

              <a
                title="aggiorna stato versamento"
                class="btn btn-link"
                data-toggle="modal"
                data-target="#update_stato_versamento_modal_btn"
                @click="updateStatusGE(item.idco, item.model)"
              >
                <i class="fa fa-refresh" aria-hidden="true"></i>
              </a>

              <a
                title="lista dello stato di versamento"
                class="btn btn-link"
                data-toggle="modal"
                data-target="#stato_versamento_modal_btn"
                @click="getStatusGE(item.company_id, item.idco, item.model)"
              >
                <i class="fa fa-sort-amount-desc" aria-hidden="true"></i>
              </a>

              <a
                title="reset per i versamenti falliti"
                class="btn btn-link"
                @click="resetStatoVersamento(item.retention_id)"
              >
                <i class="fa fa-wrench" aria-hidden="true"></i>
              </a>

              <a
                title="cambia stato a conservato"
                class="btn btn-link"
                data-toggle="modal"
                data-target="#done_btn"
                @click="done_id = item.retention_id"
              >
                <i class="fa fa-check" aria-hidden="true"></i>
              </a>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- modal pdv detail start -->
    <div
      class="modal fade bd-example-modal-lg"
      id="pdv_detail_modal_btn"
      tabindex="-1"
      role="dialog"
      aria-labelledby="pdv_detail_modal"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="pdv_detail_modal">
              Dettagli PDV in tabella CodeConservazioni.Coda
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <table class="table">
              <thead>
                <th>PDV</th>
                <th>stato in Coda</th>
                <th>Motivo Di Scarto</th>
              </thead>
              <tbody>
                <tr v-for="(item, index) in detailRetentionList" :key="index">
                  <td>{{ index }}</td>
                  <td>{{ item }}</td>
                  <td>
                    <i
                      class="fa fa-external-link"
                      aria-hidden="true"
                      @click="openErrorPdv(index)"
                    ></i>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              Close
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- modal pdv detail end -->

    <!-- modal azige | isc stato versamento start -->
    <div
      class="modal fade bd-example-modal-lg"
      id="stato_versamento_modal_btn"
      tabindex="-1"
      role="dialog"
      aria-labelledby="stato_versamento_modal"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="stato_versamento_modal">
              Stati in aziGE || ISC
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <table class="table">
              <thead>
                <th>anno</th>
                <th>stato versamento</th>
                <th>quatita</th>
              </thead>
              <tbody v-if="getStatoVersamentoList.length > 0">
                <tr
                  v-for="(item, index) in getStatoVersamentoList"
                  :key="index"
                >
                  <td>{{ item.anno }}</td>
                  <td>{{ item.stato }}</td>
                  <td>{{ item.tot }}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              Close
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- modal azige | isc stato versamento end -->

    <!-- update quantity start -->
    <div
      class="modal fade bd-example-modal-lg"
      id="update_stato_versamento_modal_btn"
      tabindex="-1"
      role="dialog"
      aria-labelledby="stato_versamento_modal"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="stato_versamento_modal">
              Aggiorna gli stati in aziGE || ISC
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">{{ updateStatoResult }}</div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              Close
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- update quantity end -->

    <!-- modal retention done start -->
    <div
      class="modal fade bd-example-modal-lg"
      id="done_btn"
      tabindex="-1"
      role="dialog"
      aria-labelledby="done_modal"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="done_modal">
              Cabia da qualsiasi stato a conservato
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">{{ done_id }}</div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              Close
            </button>
            <button
              type="button"
              class="btn btn-primary"
              data-dismiss="modal"
              @click="done"
            >
              Conferma
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- modal retention done end -->
    <v-pagination
      v-model="currentPage"
      :page-count="totalPages"
      :classes="bootstrapPaginationClasses"
      :labels="paginationAnchorTexts"
      @change="submit"
    ></v-pagination>
  </div>
</template>
<script>
import vPagination from "vue-plain-pagination";

export default {
  components: { vPagination },
  data() {
    return {
      currentPage: 1,
      totalPages: 30,
      bootstrapPaginationClasses: {
        ul: "pagination",
        li: "page-item",
        liActive: "active",
        liDisable: "disabled",
        button: "page-link",
      },
      paginationAnchorTexts: {
        first: "First",
        prev: "Previous",
        next: "Next",
        last: "Last",
      },
      idco: null,
      origin_id: null,
      status: null,
      statusList: [
        { id: 0, message: "da conservare" },
        { id: 1, message: "in coda" },
        { id: 2, message: "da controllare" },
        { id: 3, message: "conservato" },
        { id: 4, message: "manca sog.prod." },
        { id: 5, message: "non ci sono da versare" },
        { id: 6, message: "modello non associato" },
      ],
      orderList: [
        { key: "model", message: "Modello" },
        { key: "retention_quantity", message: "Quantita'" },
        { key: "status", message: "Stato" },
        { key: "error", message: "Errore" },
        { key: "created_at", message: "Data Creata" },
        { key: "updated_at", message: "Data Modifica" },
      ],
      data: [],
      tot: null,
      detailRetentionList: [],
      getStatoVersamentoList: [],
      updateStatoResult: "",
      done_id: null,
      apiURL:
        "https://archicons.archivium-srl.it/ArchiHub/procedures/varie/retention/api.php?type=",
      hasData: false,
      sort: "retention_id",
      order: "desc",
      offset: 0,
      limit: 100,
    };
  },
  mounted() {
    this.submit();
  },
  filters: {
    liveSubstr: function (string) {
      if (string == null) {
        return "";
      }
      return string.substring(0, 50) + "...";
    },
  },
  methods: {
    submit() {
      var idco = this.idco;
      //   var status = this.status;
      // var origin_id = this.origin_id;
      //   console.log("idco => " + idco);
      //   console.log("origin_id => " + origin_id);
      //   console.log("status => " + status);
      var condition = "";
      condition += idco !== null ? "&idco=" + idco : "";
      condition +=
        this.origin_id !== null ? "&origin_id=" + this.origin_id : "";
      condition +=
        this.status !== "null" && this.status !== null
          ? "&status=" + this.status
          : "";
      //   console.log(condition);
      this.$store.dispatch("loading");
      this.axios
        .get(
          this.apiURL +
            "listRetention" +
            condition +
            "&sort=" +
            this.sort +
            "&order=" +
            this.order +
            "&offset=" +
            (this.currentPage - 1) * 100 +
            "&limit=" +
            this.limit
        )
        .then((r) => {
          this.data = r.data;
          this.hasData = r.data.rows.length > 0 ? true : false;
          this.tot = r.data.total;
          this.totalPages = Math.ceil(r.data.total / this.limit);
          this.$store.dispatch("loaded");
        });
    },
    getStatusText(value) {
      var state = "";
      switch (value) {
        case 0:
          state = "da conservare";
          break;
        case 1:
          state = "in coda";
          break;
        case 2:
          state = "da controllare";
          break;
        case 3:
          state = "conservato";
          break;
        case 4:
          state = "manca sog.prod.";
          break;
        case 5:
          state = "non ci sono da versare";
          break;
      }

      return state;
    },
    detailRetention(id) {
      this.$store.dispatch("loading");
      this.axios.get(this.apiURL + "pdvDetail&id=" + id).then((r) => {
        this.detailRetentionList = r.data;
        this.$store.dispatch("loaded");
      });
    },
    openErrorPdv(pdv) {
      window.open(this.apiURL + "getTxtStream&pdv=" + pdv, "_blank");
    },
    updateStatusGE(idco, model) {
      var link =
        "https://archicons.archivium-srl.it/ArchiHub/procedures/varie/retention/api.php?type=updateStatusDetail&idco=" +
        idco +
        "&model=" +
        model;
      this.$store.dispatch("loading");
      this.axios.get(link).then((r) => {
        this.updateStatoResult = r.data;
        this.$store.dispatch("loaded");
      });
    },
    getStatusGE(company_id, idco, model) {
      var id = "";
      switch (model) {
        case "FatturaElettronica":
        case "FEPassiva":
        case "NotificaFatturaPA":
          id = company_id;
          break;
        default:
          id = idco;
          break;
      }
      var link =
        "http://archicons.archivium-srl.it/ArchiHub/procedures/varie/p/test/apiftp.php?type=getstatoversamento&id=" +
        id +
        "&model=" +
        model;
      this.$store.dispatch("loading");
      this.axios.get(link).then((r) => {
        this.getStatoVersamentoList = r.data;
        this.$store.dispatch("loaded");
      });
    },
    resetStatoVersamento(retention_id) {
      var link =
        "https://archicons.archivium-srl.it/ArchiHub/procedures/varie/retention/api.php?type=resetRetention&retention_id=" +
        retention_id;
      this.$store.dispatch("loading");
      this.axios.get(link).then((r) => {
        // this.getStatoVersamentoList = r.data;
        alert("reset => " + r.data);
        this.$store.dispatch("loaded");
      });
    },
    done() {
      console.log(this.done_id);
      var link =
        "https://archicons.archivium-srl.it/ArchiHub/procedures/varie/retention/api.php?type=retentionDone&retention_id=" +
        this.done_id;
      this.$store.dispatch("loading");
      this.axios.get(link).then((r) => {
        // this.getStatoVersamentoList = r.data;
        alert("stato cambiato => " + r.data);
        this.$store.dispatch("loaded");
      });
    },
  },
};
</script>
